.tb_avg_rating__ {
       display: flex;
       align-items: center;
}

.tb_bt_avg_rating {
       font-family: var(--tb-author-font);
       color: var(--tb-author-color);
       font-size: 20px;
       font-weight: 600;
       margin-right: 10px;
}

.tb_bt_rating_wrap {
       display: flex;
       align-items: center;
       margin-bottom: 3px;
}

.tb_bt_review_link {
       font-family: var(--tb-font);
       color: var(--tb-author-color);
       font-size: 10px;
       font-weight: 300;
       padding: 2px 0;
}

.tb_avg_rating_ico__ {
       font-size: 14px;
       position: relative;
       display: flex;
       margin: 2px;
       color: #c5c5c5;
}

.tb_avg_rating_ico_fill__ {
       position: absolute;
       left: 0;
       top: 0;
       overflow: hidden;
}