.tb_error_page {
    position: relative;
    min-height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 30px 15px;
    line-height: 1.5;
}

.tb_error_page_bg {
    background-position: top center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
    background-color: #fff;
    width: 100%;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    z-index: 0;
    opacity: .5;
}

.tb_error_page_wrapper{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    max-width: 700px;
    min-height: 200px;
    position: relative;
    z-index: 1;
    background-color: #fff;
    padding: calc(10px + .6vh + .6vw);
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.06), 0 2px 4px rgba(0, 0, 0, 0.04), 0 5px 14px rgba(0, 0, 0, 0.02);
    border-radius: 4px;
}

.tb_error_title {
    font-size: calc(.4vh + .4vw + 14px);
    text-align: center;
    margin-bottom: 10px;
    font-weight: 600;
}

.tb_error_des {
    text-align: center;
    display: block;
    font-size: calc(.15vh + .15vw + 10px);
    margin-bottom: 10px;
    color: #545454;
    font-weight: 400;
}

.tb_error_highlight{
    color: rgb(255, 64, 81);
    font-weight: 600;
    display: inline;
}
.tb_tagEmbed .tb_error_highlight {
    color: #4179ff;
}
.tb_tagshop .tb_error_highlight {
    color: #4e69ed;
}
.tb_error_des a {
    color: #ff4051;
    text-decoration: none;
    font-weight: 500;
}

.tb_tagEmbed .tb_error_des a {
    color: #4179ff;
}

.tb_tagshop .tb_error_des a {
    color: #4e69ed;
}

.tb_error_page_actions {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

.tb_error_action_list {
    padding: 10px;
}

.tb_error_btn_primary, .tb_error_btn_secondary{
    display: inline-block;
    font-weight: 500;
    color: #2b2b2b;
    text-align: center;
    vertical-align: middle;
    padding: 0.5rem .8rem;
    font-size: 13px;
    line-height: 1.5;
    border: 1px solid transparent;
    border-radius: 3px;
    cursor: pointer;
    min-width: 130px;
    text-decoration: none;
    transition: color .15s ease,background-color .15s ease,border-color .15s ease,box-shadow .15s ease;
}

.tb_error_btn_primary{
    color: #fff;
    background-color: #ff4051;
    border-color: #ff4051;
}

.tb_tagEmbed .tb_error_btn_primary{
    color: #fff;
    background-color: #4179ff;
    border-color: #4179ff;
}

.tb_tagshop .tb_error_btn_primary{
    color: #fff;
    background-color: #4e69ed;
    border-color: #4e69ed;
}

.tb_error_btn_secondary{
    color: #2b2b2b;
    background-color: transparent;
    border-color: #2b2b2b;
}

.tb_error_btn_primary:hover, .tb_error_btn_primary:focus{
    color: #fff;
    background-color: #e23442;
    border-color: #e23442;
}

.tb_tagEmbed .tb_error_btn_primary:hover, .tb_tagEmbed .tb_error_btn_primary:focus{
    color: #fff;
    background-color: #4179ff;
    border-color: #4179ff;
    opacity: .95;
}

.tb_tagshop .tb_error_btn_primary:hover, .tb_tagshop .tb_error_btn_primary:focus{
    color: #fff;
    background-color: #4e69ed;
    border-color: #4e69ed;
    opacity: .95;
}
.tb_error_btn_secondary:hover, .tb_error_btn_secondary:focus{
    color: #fff;
    background-color: #2b2b2b;
    border-color: #2b2b2b;
}

.tb_error_page_logo_ico {
    background-repeat: no-repeat;
    background-position: right top;
    height: 30px;
    background-size: contain;
    width: 200px;
}

.tb_error_page_logo {
    position: absolute;
    bottom: -40px;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.tb_error_onsite_btn{
    position: relative;
    z-index: 1;
    margin-bottom: 30px;
}
.tb_error_page.tb_error_theme_16,
.tb_error_page.tb_error_theme_55{
    min-height:300px;
}
.tb_error_page.tb_error_theme_wall_error,
.tb_error_page.tb_error_theme_20,
.tb_error_page.tb_error_theme_5,
.tb_error_page.tb_error_theme_19,
.tb_error_page.tb_error_theme_3,
.tb_error_page.tb_error_theme_4,
.tb_error_page.tb_error_theme_50{
    min-height:400px;
}

.tb_error_page.tb_error_theme_47{
    min-height:400px;
}

.tb_error_page.tb_error_theme_49,
.tb_error_page.tb_error_theme_52,
.tb_error_page.tb_error_theme_60{
    min-height:600px;
}
.tb_error_title, .tb_error_des, .tb_error_btn_primary, .tb_error_btn_secondary{
    font-family: inherit;
}