.tb_app_wrapper {
    background-attachment: fixed;
    background-size: cover;
    background-position: top center;
    background-repeat: no-repeat;
    flex-direction: column;
}
.tb_bold_txt__{
    font-weight: 600;
    display: block;
    margin-bottom: 5px;
}

.tb_text_links_:before,
.tb_text_links_:after{
    display: none !important;
}
.text_links_{
    color: #3333FF!important;
    font-weight: 500;
    text-decoration: none
}

.tb_content_line-1,
.tb_content_line-2,
.tb_content_line-3,
.tb_content_line-4,
.tb_content_line-5,
.tb_content_line-6{
    display: -webkit-box;   
    -webkit-box-orient: vertical;     
    overflow: hidden; 
}
.tb_content_line-1{
    -webkit-line-clamp: 1;   
}
.tb_content_line-2{
    -webkit-line-clamp: 2;   
}
.tb_content_line-3{
    -webkit-line-clamp: 3;   
}
.tb_content_line-4{
    -webkit-line-clamp: 4;   
}
.tb_content_line-5{
    -webkit-line-clamp: 5;   
}
.tb_content_line-6{
    -webkit-line-clamp: 6;   
}

.tb_read_more_{
    display: inline-block;
    white-space: nowrap;
    cursor: pointer;
    opacity: .5;
    font-family: var(--tb-font);
    color: var(--tb-color);
    font-size: var(--tb-font-size);
    line-height: var(--tb-line-height);
    transition: opacity .3s ease;
}
.tb_read_more_:hover{
    opacity: 1;
}

.tb_blur_bg_:after{
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    background: rgba(0, 0, 0, .35);
    content: "";
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
}
.tb_blur_bg_{
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    height: 100%;
    margin: 0 auto;
    left: auto;
    right: auto;
    top: 0;
    width: 100%;
}

@import "../Elements/scss/Elements.scss";
