.tb_onsite_upload_btn_wrap{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
    align-items: center;
    -webkit-justify-content: center;
    justify-content: center;
    padding: 10px;
}
.tb_onsite_upload_btn{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    max-width: 350px;
}
.tb_onsite_btn{
    padding: 6px;
    display: flex;
    align-items: stretch;
    justify-content: center;
    border-radius: 3px;
    cursor: pointer;
    background-color: var(--tb-snap-bg-color);
}
.tb_onsite_qr_btn_wrap{
    display: flex;
    align-items: stretch;
    justify-content: center;
}

.tb_onsite_qr_code{
    object-fit: cover;
    background: #fff;
    color:  var(--tb-snap-color);
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.tb_onsite_upload_btn .btn-scan canvas{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.tb_onsite_btn_txt{
    font-size: 14px;
    font-weight: 400;
    padding:0 10px;
    display: flex;
    align-items: center;
    text-align: center;
    padding: 6px;
    font-family: var(--tb-snap-font);
    font-weight: var(--tb-snap-varient);
    color:  var(--tb-snap-color);
}

.tb_onsite_qr_code + .tb_onsite_btn_txt{
    text-align: left;
}


.tb_onsite_post__ .tb_onsite_upload_btn_wrap{
    padding: 0;
    padding-bottom: 100%;
    position: relative;
}
.tb_onsite_post__ .tb_onsite_upload_btn{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    height: 100%;
}

.tb_onsite_post__ .tb_onsite_btn{
    height: 100%;
    width: 100%;
}
.tb_onsite_post__ .tb_onsite_qr_btn_wrap{
    flex-direction: column;
    width: 100%;
}

.tb_onsite_post__ .tb_onsite_qr_btn_wrap div:last-child{
    margin-bottom: 0;
}

.tb_onsite_post__  .tb_onsite_qr_code{
    background-color: transparent;
    margin-bottom: 8px;
}

.tb_onsite_post__ .tb_onsite_qr_code svg {
    width: 100%;
    height: 100%;
    max-width: 50%;
}

.tb_onsite_post__ .tb_onsite_btn_txt{
    padding: 0;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;  
    overflow: hidden;
}

.tb_onsite_post__ .tb_onsite_qr_code + .tb_onsite_btn_txt{
    text-align: center;
    -webkit-line-clamp: 2;
}